//chart-base 作用
<template>
  <div class="chart-base" ref="eChart" :class="{ready: ready}"></div>
</template>

<script>
import echarts from "echarts";

function autoChart(chart, option, app) {
  const interval = setInterval(function() {
    var dataLen = option.series[0].data.length;

    // 取消之前高亮的图形
    chart.dispatchAction({
      type: "downplay",
      seriesIndex: 0,
      dataIndex: app.currentIndex
    });
    app.currentIndex = (app.currentIndex + 1) % dataLen;
    // 高亮当前图形
    chart.dispatchAction({
      type: "highlight",
      seriesIndex: 0,
      dataIndex: app.currentIndex
    });
    // 显示 tooltip
    chart.dispatchAction({
      type: "showTip",
      seriesIndex: 0,
      dataIndex: app.currentIndex
    });
  }, 1000);
  return function() {
    clearInterval(interval);
  };
}

export default {
  name: "chart-base",
  props: {
    data: Object,
    option: Object,
    autoTooltip: {
      type: Boolean,
      default: false
    },
    loopOption: {
      type: Object,
      default() {
        return {
          loopSeries: true
        };
      }
    }
  },
  data() {
    return {
      instance: null,
      ready: false
    };
  },
  methods: {
    initEchart() {
      /**
       *
       */
      // 清除自动轮播
      this.clearLoop && this.clearLoop();
      if (this.instance) {
        this.instance.clear();
      } else {
        this.instance = echarts.init(this.$refs.eChart);
      }
      this.instance.showLoading();
      this.instance.setOption(this.option);

      if (this.autoTooltip) {
        const option = this.instance.getOption();
        this.clearLoop = autoChart(this.instance, option, { currentIndex: -1 });
      }
      this.instance.hideLoading();
      this.ready = true;
    },
    updateChart() {
      this.instance.showLoading();
      // 清除自动轮播
      this.clearLoop && this.clearLoop();
      this.instance.setOption(this.data);
      if (this.autoTooltip) {
        const option = this.instance.getOption();
        this.clearLoop = autoChart(this.instance, option, { currentIndex: -1 });
      }
      this.instance.hideLoading();
      // this.ready = true
    },
    updateSize() {
      this.instance && this.instance.resize();
    }
  },
  watch: {
    data() {
      this.updateChart();
    }
  },
  mounted() {
    this.initEchart();
    this.data && Object.keys(this.data).length && this.updateChart();
    window.addEventListener("resize", this.updateSize);
  },
  beforeDestroy() {
    this.instance = null;
    this.clearLoop && this.clearLoop();
    window.removeEventListener("resize", this.updateSize);
  }
};
</script>

<style lang="scss" scoped>
.chart-base {
  opacity: 0;
  transform: translate3d(50px, 0, 0);
  transition: opacity 0.3s, transform 1s;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  &.ready {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
</style>
