/**
 * 日期格式化
 * @param time {number|string} 时间戳, 可选, 默认为当前时间
 * @param format {string} 格式序列, 可选, 默认为'{y}-{m}-{d} {h}:{i}:{s}'
 * @return {*}
 */
export function parseTime (time = new Date(), format = '{y}-{m}-{d} {h}:{i}:{s}') {
  let date
  if (typeof time === 'object') {
    date = time
  } else {
    if ((time + '').length === 10) {
      time = +time * 1000
    }
    date = new Date(time - 0)
  }
  const formatObj = {
    y: date.getFullYear(),
    m: date.getMonth() + 1,
    d: date.getDate(),
    h: date.getHours(),
    i: date.getMinutes(),
    s: date.getSeconds(),
    a: date.getDay()
  }
  const timeStr = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
    let value = formatObj[key]
    if (key === 'a') return ['一', '二', '三', '四', '五', '六', '日'][value - 1]
    if (result.length > 0 && value < 10) {
      value = '0' + value
    }
    return value || 0
  })
  return timeStr
}

/**
 * 时间人性化格式化
 * @param time {number|string} 时间戳
 * @param format {string} 格式序列
 * @return {*}
 */
export function formatTime (time, format) {
  if (arguments.length === 0) {
    return null
  }

  if ((time + '').length === 10) {
    time = +time * 1000
  }
  const d = new Date(time)
  const now = Date.now()

  const diff = (now - d) / 1000

  if (diff < 30) {
    return '刚刚'
  } else if (diff < 3600) { // less 1 hour
    return Math.ceil(diff / 60) + '分钟前'
  } else if (diff < 3600 * 24) {
    return Math.ceil(diff / 3600) + '小时前'
  } else if (diff < 3600 * 24 * 2) {
    return '1天前'
  }
  if (format) {
    return parseTime(time, format)
  } else {
    return d.getMonth() + 1 + '月' + d.getDate() + '日' + d.getHours() + '时' + d.getMinutes() + '分'
  }
}

/**
 * 格式化日期时间字符串
 * 例：20180913123431 => 2018-09-13 12:34:31
 * @param timeString {string} 日期时间字符串
 * @param format {string} 默认值：'xxxx-xx-xx xx:xx:xx' 使用 x 作为占位符表示目标格式，最终会逐位替换为 timeString 中的数字
 * @return {string}
 */
export function formatTimeString (timeString, format) {
  const timeStringArray = timeString.split('')
  return (format || 'xxxx-xx-xx xx:xx:xx').replace(/x/g, () => timeStringArray.splice(0, 1))
}

/**
 * 数字格式化
 * @param num {number}
 * @param digits {number} 小数位数
 * @return {string}
 */
export function nFormatter (num, digits) {
  const si = [
    { value: 1E18, symbol: 'E' },
    { value: 1E15, symbol: 'P' },
    { value: 1E12, symbol: 'T' },
    { value: 1E9, symbol: 'G' },
    { value: 1E6, symbol: 'M' },
    { value: 1E3, symbol: 'k' }
  ]
  for (let i = 0; i < si.length; i++) {
    if (num >= si[i].value) {
      return (num / si[i].value + 0.1).toFixed(digits).replace(/\.0+$|(\.[0-9]*[1-9])0+$/, '$1') + si[i].symbol
    }
  }
  return num.toString()
}

/**
 * 金额格式化，每隔三位插入逗号
 * @param price {number|string}
 * @return {string}
 */
export function formatPrice (price) {
  let strArray = ((price || 0) - 0).toFixed(2).split('.')
  return (strArray[0] - 0).toLocaleString('en-US') + '.' + strArray[1]
}

/**
 * 带单位数值格式化
 * @param value {number|string}
 * @param unit {string} 单位
 * @param digits {number} 小数位数
 * @return {string|string}
 */
export function formatUnit (value, unit, digits) {
  let str = ''
  value = value - 0
  // 如果代为是这些，则不保留小数点
  let i = ['块', '只', '个', '袋', '头', '辆', '架', '对', '张', '顶', '匹', '单', '支', '箱']
  if (i.indexOf(unit) > -1) {
    str = value.toFixed(0) + unit
  } else {
    str = value.toFixed(digits || 3) + unit
  }
  return str
}

/**
 * 格式化字符串，字符串分段，默认从左到右每四位插入一个空格
 * 例：1234567890 => 123 456 789 0
 * @param string {string} 原始字符串
 * @param length {number} 每段字符串长度: 默认为 4
 * @param spacer {string} 间隔符: 默认为空格
 * @param ltr {boolean} 是否从左至右: 默认为 true
 * @return {string}
 */
export function formatString (string, { length = 4, spacer = ' ', ltr = true } = {}) {
  if (!string && string !== 0) return ''

  string = string.toString()

  if (string.length <= length) return string

  let str = ''
  let i
  const strList = []

  if (ltr) {
    i = 0
    while (i < string.length) {
      strList.push(string.substr(i, length))
      i += length
    }
    str = strList.join(spacer)
  } else {
    i = string.length - length
    while (i + length >= 0) {
      strList.push(i > 0 ? string.substr(i, length) : string.substr(0, length + i))
      i -= length
    }
    str = strList.reverse().join(spacer)
  }
  return str
}

/**
 * 返回截取后的字符串
 * @param string {string} 源字符串
 * @param length {number} 截取长度
 * @param ltr {boolean} 是否从左到右截取， 默认为 true
 * @param start {number} 开始截取的位置， 如果 ltr 为 false， 则应该为负值，从 -1 开始
 * @param replace {string} 替换字符串，传入后，默认被裁掉的字符串将被替换为该字符串
 * @return {string}
 */
export function stringSplice (string, length, { ltr = true, start = 0, replace = '' }) {
  if (!string) return ''
  if (string.length <= length) replace = ''
  if (ltr) {
    return string.substr(start, length) + replace
  } else {
    return replace + string.substr(1 - length + start, length)
  }
}

/**
 * 数字保留小数位数
 * @param number {number|string} 源数字或字符串
 * @param digits {number} 保留小数位数
 * @param looseMode {boolean} 宽松模式, 默认为 false
 * 在宽松模式下，仅处理小数位数多于 digits 的数字，即，小数位数少于 digits 的数字不被处理
 * @return {string}
 */
export function fixNumber (number, digits, looseMode = false) {
  if (!/^[-|\d]?\d+.?\d+$/.test(number)) return number
  if (!looseMode) return (+number).toFixed(digits)
  const digitsLength = (number.toString().split('.')[1] || '').length
  if (digitsLength <= digits) return number.toString()
  return (+number).toFixed(digits)
}
