<template>
    <transition v-on:before-enter="beforeEnter"
                :enter-active-class="enter"
                :leave-active-class="leave"
    >
        <div class="dashboard-text-container"
             v-if="animation"
             :style="styleObject"
        >
            {{label}}
        </div>
    </transition>
</template>

<script>
    import animate from '@/mixins/animate'

    export default {
        name  : "DashboardText",
        mixins: [animate],
        props : {
            styleObject: {
                type: Object,
                default() {
                    return {}
                }
            },
            label      : {
                type   : String,
                default: ''
            }
        }
    }
</script>

<style scoped>

</style>
