import axios from 'axios'

export async function post(url, params) {
  try {
    let { data } = await axios.post(url, params)

    if (data.code === '0') {
      return { data: data.data, message: '成功' }
    } else {
      throw new Error(data.msg)
    }
  } catch (e) {
    throw e
  }
}
