<template>
  <dashboard-panel :style-object="newStyle">
    <div class="container-inner" ref="container" :style="{fontSize:`${fontSize}px`}">
      <div class="carouse-table-header clearfix">
        <div
          class="carouse-table-cell"
          v-for="(cell,index) in columns"
          :key="index"
          :style="cellStyle(cell,true)"
        >{{cell.name}}</div>
      </div>
      <div class="carouse-table-body" :style="{top:`${itemHeight}px`}">
        <swiper :style="{height: '100%'}" :options="swiperOption">
          <swiper-slide v-for="(row,index) in rows" :key="`${key}-${index}`">
            <div class="carouse-table-row clearfix" :style="itemStyle">
              <div
                class="carouse-table-cell"
                v-for="(item,x) in Object.keys(row).map((key) =>{ return row[key]})"
                :key="`${key}-${index}-${x}`"
                :style="cellStyle(columns[x])"
              >
                <div :class="x==0? 'warn-bg' :''">{{item||'-'}}</div>
              </div>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
  </dashboard-panel>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "CarouselTable",
  props: {
    option: {
      type: Object,
      default() {}
    }
  },
  data() {
    return {
      dataType: "json",
      chartConfig: {},
      chartData: {},
      style: {
        position: "absolute",
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        paddingLeft: "10px"
      },
      lineNumber: 5,
      itemHeight: 0,
      key: 0,
      swiperOption: {
        direction: "vertical",
        autoplay: true,
        slidesPerView: 4
      }
    };
  },
  computed: {
    newStyle() {
      const style = this.style || {};
      const styleObject = this.option.wrapperStyle || {};
      return { ...style, ...styleObject };
    },
    fontSize() {
      return 18;
    },
    columns() {
      const { columns = [] } = this.chartConfig || {};
      return columns;
    },
    rows() {
      const { rows = [] } = this.chartData || {};
      return rows;
    },
    itemStyle() {
      const itemHeight = this.itemHeight;
      return { height: `${itemHeight}px`, lineHeight: `${itemHeight}px` };
    }
  },
  watch: {
    chartData() {
      this.key = new Date().getTime();
    },
    option: {
      handler(option = {}) {
        const { data = {}, config = {} } = option;
        this.chartData = data;
        this.chartConfig = config;
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    this.onResize();
    window.addEventListener("resize", this.onResize);
  },
  methods: {
    cellStyle(style = {}, isHeader) {
      const itemStyle = this.itemStyle;
      let cellStyle = {
        ...itemStyle
      };

      const colLength = this.columns.length;

      cellStyle.width =
        typeof style.width === "undefined"
          ? `${100 / colLength}%`
          : style.width;
      cellStyle.align =
        typeof style.align === "undefined" ? "center" : style.align;

      if (!isHeader) {
        if (style.cellColor) {
          cellStyle.color = style.cellColor;
        }
        if (style.cellBg) {
          cellStyle.background = style.cellBg;
        }
        if (style.cellAlign) {
          cellStyle.textAlign = style.cellAlign;
        }
      }
      return cellStyle;
    },
    getItemHeight(itemNumber = 1) {
      const height = this.$refs.container.offsetHeight;
      const lineNumber = itemNumber || 1;
      return height / lineNumber;
    },
    onResize() {
      this.itemHeight = this.getItemHeight(this.lineNumber);
    }
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>

<style lang="scss" scoped>
.container-inner {
  width: 100%;
  height: 100%;
  position: relative;
}

.carouse-table-header {
  color: #1edbf8;
  background-repeat: repeat-x;
  background-size: contain;
  text-align: center;
}

.carouse-table-body {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  color: #fff;
  overflow: hidden;
}

.carouse-table-cell {
  float: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: center;
}
.warn-bg {
  background-image: url("~@/assets/nb-lot/warn-bg.svg");
  background-size:100% 100%;
}
</style>
