<template>
    <transition v-on:before-enter="beforeEnter"
                :enter-active-class="enter"
                :leave-active-class="leave"
    >
        <div v-if="animation" :style="styleObject">
            <slot></slot>
        </div>
    </transition>
</template>

<script>
    import animate from '@/mixins/animate'

    export default {
        name  : "DashboardPanel",
        mixins: [animate],
        props : {
            styleObject: {
                type: Object,
                default() {
                    return {}
                }
            }
        }

    }
</script>

