<template>
    <dashboard-panel :style-object="newStyle">
        <chart-base :data="data" :option="option" :autoTooltip="autoTooltip"/>
    </dashboard-panel>
</template>

<script>

    import ChartBase from '../ChartBase'

    export default {
        name      : "ChartItem",
        props     : {
            option     : {
                type: Object,
                default() {
                    return {}
                }
            },
            data       : {
                type: Object,
                default() {
                    return {}
                }
            },
            autoTooltip: {
                type   : Boolean,
                default: false
            },
            styleObject: {
                type: Object,
                default() {
                    return {}
                }
            }
        },
        mounted(){
        },
        computed  : {
            newStyle() {
                const style = this.style || {}
                const styleObject = this.styleObject || {}
                return {...style, ...styleObject}
            }
        },
        data() {
            return {
                style: {
                    position: 'absolute',
                    left    : 0,
                    right   : 0,
                    top     : 0,
                    bottom  : 0
                }
            }
        },
        components: {
            ChartBase
        }
    }
</script>

<style scoped>

</style>
