import { post } from '@/fetch'

export default {
  data() {
    return {
      dataType: 'json',
      chartConfig: {},
      chartData: {},
      _getDataInterval: 10000,
      clearTimeout: null
    }
  },
  watch: {
    option: {
      handler(option = {}) {
        const { data = {}, config = {} } = option
        this.chartData = data
        this.chartConfig = config
        const { dataType, interval = 100000, apiParams, apiUrl } = config
        // 数据类型 / 数据库/ 接口请求 / 静态json
        // sql,api,json
        this.dataType = dataType || 'json'
        // 刷新间隔
        this._getDataInterval = interval
        // 参数
        this.apiParams = apiParams || {}
        // 请求地址
        this.apiUrl = apiUrl || ''
      },
      immediate: true,
      deep: true
    },
    dataType: {
      handler(val) {
        clearTimeout(this.clearTimeout)
        if (val === 'api') {
          this.getData()
        }
      },
      immediate: true
    }
  },
  methods: {
    async getData() {
      try {
        const url = this.apiUrl || ''
        const params = this.apiParams || {}
        const { data } = await post(url, params)
        this.chartData = data
        if (this._getDataInterval > 0) {
          this.clearTimeout = setTimeout(() => {
            this.getData()
          }, this._getDataInterval)
        }

      } catch (e) {
        this.$message({
          message: e.message,
          type: 'error'
        })
      } finally {
        // this.loading = false
      }
    }
  }
}
