<template>
  <div class="widget p0 chart-base" ref="eChart" :class="{ready: ready}"></div>
</template>

<script>
export default {
  name: 'chart-base',
  props: {
    data: Object,
    option: Object
  },
  data () {
    return {
      instance: null,  
      ready: false  
    }
  },
  methods: {
    initEchart () {
      if (this.instance) {
        this.instance.clear()  
      } else {
        this.instance = window.echarts.init(this.$refs.eChart)
      }
      this.instance.setOption(this.option)
      this.instance.showLoading()
    },
    updateChart () {
      this.instance.setOption(this.data)
      this.instance.hideLoading()
      this.ready = true
    },
    updateSize () {
      window.setTimeout(()=>{
        console.log('chart-resize');
        this.instance && this.instance.resize()
      },1000)
    }
  },
  watch: {
    data (value) {
      this.updateChart()
    }
  },
  mounted () {
    this.initEchart()
    this.data && Object.keys(this.data).length && this.updateChart()
    window.addEventListener('resize', this.updateSize)
  },
  beforeDestroy () {
    this.instance = null;
    window.removeEventListener('resize', this.updateSize)
  }
}
</script>

<style lang="scss" scoped>
.chart-base {
  opacity: 0;
  transform: translate3d(50px, 0, 0);
  transition: opacity .3s, transform 1s;
  &.ready{
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
</style>
