<template>
  <chart-item
    :data="data"
    :option="chartOption"
    :autoTooltip="option.autoTooltip"
    :style-object="option.wrapperStyle"
  />
</template>

<script>
import ChartItem from "../ChartItem";
import getData from "@/mixins/getData";
const OPTION = {
  tooltip: {
    trigger: "axis"
  },
  xAxis: {
    type: "category",
    boundaryGap: false,
    data: [],
    splitLine: {
      show: false,
      lineStyle: {
        color: "#56C1FF",
        opacity: 0.3
      }
    },
    axisLine: {
      show: true,
      lineStyle: {
        color: "#56C1FF",
        opacity: 0.3
      }
    },
    axisLabel: {
      color: "#fff",
      interval: 0
    },
    axisTick: {
      show: false
    }
  },
  grid: {
    left: "3%",
    right: "5%",
    bottom: "0%",
    top: "35",
    containLabel: true
  },
  yAxis: {
    type: "value",
    textStyle: {
      color: "#fff",
    },
    splitLine: {
      show: true,
      lineStyle: {
        color: "#56C1FF",
        opacity: 0.3,
        type: "dashsed"
      }
    },
    nameTextStyle: {
      color: "#fff"
    },
    axisLine: {
      show: false,
      lineStyle: {
        color: "#56C1FF",
        opacity: 0.3
      }
    },
    axisLabel: {
      color: "#fff",
      fontSize:16,
      interval: 0
    },
    axisTick: {
      show: false
    }
  },
  series: [
    {
      data: [],
      type: "line",
      animation:true,
      areaStyle: {}
    }
  ]
};
export default {
  name: "ChartAreaBasic",
  mixins: [getData],
  props: {
    styleObject: {
      type: Object,
      default() {
        return {};
      }
    },
    option: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      chartOption: OPTION
    };
  },
  computed: {
    data() {
      const data = this.chartData;
      const config = this.chartConfig;
      const { categories = [], series = [], unit = "", title, subTitle } = data;
      const {
        areaOpacity = 1,
        areaColor = {
          type: "linear",
          x: 1,
          y: 1,
          x2: 0,
          y2: 0,
          colorStops: [
            {
              offset: 0,
              color: "rgba(35, 197, 255, 0.32)" // 0% 处的颜色
            },
            {
              offset: 1,
              color: "rgba(35, 197, 255, 0.32)" // 100% 处的颜色
            }
          ],
          global: false // 缺省为 false
        },
        areaColor2 = {
          type: "linear",
          x: 1,
          y: 1,
          x2: 0,
          y2: 0,
          colorStops: [
            {
              offset: 0,
              color: "rgba(33, 0, 250, 0.24)" // 0% 处的颜色
            },
            {
              offset: 1,
              color: "rgba(33, 0, 250, 0.24)" // 100% 处的颜色
            }
          ],
          global: false // 缺省为 false
        },
        lineColor2 = "#2100FA",
        lineColor = "#23C5FF",
        itemColor = "#fff",
        itemBorderColor = "#57C0FF",
        showLabel = false,
        showTitle = false,
        labelColor = "#57C0FF",
        lineWidth = 4
      } = config;

      return {
        title: {
          text: `{a|${title || ""}}  {b|${subTitle || ""}}`,
          show: showTitle,
          top: "2%",
          left: "25%",
          textStyle: {
            rich: {
              a: {
                color: "#60E6FF",
                align: "center",
                fontSize: 18
              },
              b: {
                color: "#FFFFFF",
                align: "center",
                fontSize: 18,
                fontWeight: 600
              }
            }
          }
        },
        legend: {
          show: true,
          x: "right",
          icon:'circle',
          itemWidth:8,
          itemHeight:8,
          textStyle: {
            color: "#fff",
            fontSize:16,
            fontFamily:'DONGQINGW3'
          }
        },
        xAxis: {
          data: categories,
          axisLabel:{
            fontSize:16
          }
        },
        yAxis: [
          {
            name: unit,
            // nameLocation:'center',
            nameTextStyle:{
              fontSize:16
            }
          }
        ],
        series: [
          {
            name: "预测",
            data: series[1],
            type: "line",
            smooth: true,
            symbol:'none',
            lineStyle: {
              width: lineWidth,
              color: lineColor2
            },
            label: {
              show: showLabel,
              color: labelColor
            },
            itemStyle: {
              color: '#2100FA',
              borderColor: itemBorderColor
            },
            areaStyle: {
              color: areaColor2,
              opacity: areaOpacity
            }
          },
          {
            name: "实际",
            data: series[0],
            type: "line",
            smooth: true,
            symbol:'none',
            lineStyle: {
              width: lineWidth,
              color: lineColor
            },
            label: {
              show: showLabel,
              color: labelColor
            },
            itemStyle: {
              color: '#22C4FF',
              borderColor: itemBorderColor
            },
            areaStyle: {
              color: areaColor,
              opacity: areaOpacity
            }
          }
        ]
      };
    }
  },
  components: {
    ChartItem
  }
};
</script>

<style scoped>
</style>
