import Vue from 'vue'
import Router from 'vue-router'
const _import = require('./_import_' + process.env.NODE_ENV)
// 页面导入
const _importPage = file => _import('screens/' + file)

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'screen',
    redirect: '/nb-lot'
  },
  {
    path: '/nb-lot',
    name: 'nb-lot',
    component: _importPage('nb-lot/index'),
    meta: {
      title: 'nb-lot数据大脑'
    }
  }
]
})

export default router
