//设置rem实现页面缩放
function pageResize() {
  let Cfg = {
    designH: 540,
    designW: 960
  }
  let [pageH, pageW] = [window.innerHeight, window.innerWidth];
  let isWider = pageW / pageH > Cfg.designW / Cfg.designH;
  let [scaleW, scaleH] = [pageW / Cfg.designW, pageH / Cfg.designH];
  var scale = isWider ? scaleH : scaleW;
  document.querySelector('html').style.fontSize = scale * 10 + "px"
}
export default pageResize;
